import React from "react";
import "./App.css";
import QRCode from "qrcode.react";

const downloadQR = () => {
  const canvas = document.getElementById("123456");
  const pngUrl = canvas
    .toDataURL("image/png")
    .replace("image/png", "image/octet-stream");
  let downloadLink = document.createElement("a");
  downloadLink.href = pngUrl;
  downloadLink.download = "123456.png";
  document.body.appendChild(downloadLink);
  downloadLink.click();
  document.body.removeChild(downloadLink);
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      empresa: "",
      nombre: "",
      puesto: "",
      oficina: "",
      celular: "",
      correo: "",
      direcion: "",
      isHidden: true,
      beignCard: "",
      calle: "",
      ciudad: "",
      cp: "",
      pais: ""
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(field, event) {
    const newState = { ...this.state };
    newState[field] = event.target.value;
    this.setState(newState);
  }

  handleSubmit(event) {
    const {
      nombre,
      empresa,
      oficina,
      celular,
      correo,
      calle,
      pais,
      cp,
      ciudad
    } = this.state;
    let str = "BEGIN:VCARD\n";
    str += "VERSION:3.0\n";
    str += `N:${nombre}\n`;
    str += `FN:${nombre}\n`;
    str += `ORG:${empresa}\n`;
    str += "URL:\n";
    str += `TEL;TYPE=voice,work,pref:+52 1 ${oficina}\n`;
    str += `TEL;TYPE=voice,cell,pref:+52 ${celular}\n`;
    str += `EMAIL:${correo}\n`;
    str += `ADR;TYPE=WORK,PREF:;;${calle};${ciudad};CP;${cp};${pais} \n`;
    str += "END:VCARD";
    console.log(str);

    this.setState({ isHidden: false, beignCard: str });

    event.preventDefault();
  }
  render() {
    const { isHidden, beignCard } = this.state;
    return (
      <div className="App">
        <header className="App-header">
          {isHidden ? (
            <form onSubmit={this.handleSubmit}>
              <label>
                <input
                  type="text"
                  value={this.state.empresa}
                  onChange={event => {
                    this.handleChange("empresa", event);
                  }}
                  placeholder="Empresa"
                />
              </label>
              <label>
                <input
                  type="text"
                  value={this.state.nombre}
                  onChange={event => {
                    this.handleChange("nombre", event);
                  }}
                  placeholder="Nombre"
                />
              </label>
              <label>
                <input
                  type="text"
                  value={this.state.puesto}
                  onChange={event => {
                    this.handleChange("puesto", event);
                  }}
                  placeholder="Puesto"
                />
              </label>
              <label>
                <input
                  type="text"
                  value={this.state.oficina}
                  onChange={event => {
                    this.handleChange("oficina", event);
                  }}
                  placeholder="Telefono oficina"
                />
              </label>
              <label>
                <input
                  type="text"
                  value={this.state.celular}
                  onChange={event => {
                    this.handleChange("celular", event);
                  }}
                  placeholder="Telefono Celular"
                />
              </label>
              <label>
                <input
                  type="text"
                  value={this.state.correo}
                  onChange={event => {
                    this.handleChange("correo", event);
                  }}
                  placeholder="Correo"
                />
              </label>
              <label>
                <input
                  type="text"
                  value={this.state.calle}
                  onChange={event => {
                    this.handleChange("calle", event);
                  }}
                  placeholder="Calle"
                />
              </label>
              <label>
                <input
                  type="text"
                  value={this.state.ciudad}
                  onChange={event => {
                    this.handleChange("ciudad", event);
                  }}
                  placeholder="Ciudad"
                />
              </label>
              <label>
                <input
                  type="text"
                  value={this.state.cp}
                  onChange={event => {
                    this.handleChange("cp", event);
                  }}
                  placeholder="Codigo postal"
                />
              </label>
              <label>
                <input
                  type="text"
                  value={this.state.pais}
                  onChange={event => {
                    this.handleChange("pais", event);
                  }}
                  placeholder="PaÃ­s"
                />
              </label>
              <input type="submit" value="Generar QR" />
            </form>
          ) : (
            <>
              <QRCode
                id="123456"
                value={beignCard}
                size={290}
                level={"H"}
                includeMargin={true}
              />
              <a onClick={downloadQR}>Descargar</a>
              <a
                onClick={() => {
                  this.setState({ isHidden: true });
                }}
              >
                Crear otro
              </a>
            </>
          )}
        </header>
      </div>
    );
  }
}

export default App;
